<template>
  <div>
    <!-- 二级路由出口 -->
    <router-view></router-view>
    <van-tabbar v-model="active" active-color="skyblue">
      <van-tabbar-item icon="home-o" to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="apps-o" to="/cate">分类</van-tabbar-item>
      <van-tabbar-item icon="cart-o" to="/cart" :badge="total">购物车</van-tabbar-item>
      <van-tabbar-item icon="user-o" to="/mine">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {mapActions,mapGetters} from "vuex";
export default {
  data(){
    return {
      active:0,
    }
  },
   computed:{
    ...mapGetters({
      "user":"user",
      "total":"total"
    })
  },
  methods:{
    ...mapActions({
      "reqCart":"cartListActions"
    })
  },
  mounted(){
    if(this.user){
      this.reqCart(this.user.uid)
    }
  }
}
</script>

<style>

</style>



